import React from 'react';
import WordItem from './WordItem';
const WordList = ({ words, setWords }) => {
    const saveWord = (wordObj) => {
        return new Promise((resolve) => {
            chrome.storage.local.get(['savedWords'], (result) => {
                let savedWords = result.savedWords || [];
                if (!savedWords.some((w) => w.word === wordObj.word)) {
                    savedWords.push(wordObj);
                    chrome.storage.local.set({ savedWords }, () => {
                        resolve();
                    });
                }
                else {
                    resolve();
                }
            });
        });
    };
    const saveWordHandler = (index) => {
        const wordObj = words[index];
        saveWord(wordObj).then(() => {
            const newWords = [...words];
            newWords[index] = Object.assign(Object.assign({}, newWords[index]), { saved: true });
            setWords(newWords);
        });
    };
    if (words.length === 0) {
        return (React.createElement("p", { className: "text-red-500 italic text-center text-base bg-gray-100 border border-gray-300 rounded-md p-2 mt-5" }, "No words found."));
    }
    return (React.createElement(React.Fragment, null, words.map((wordObj, index) => (React.createElement(WordItem, { key: index, wordObj: wordObj, onSave: () => saveWordHandler(index) })))));
};
export default WordList;
