import React from 'react';
const MemorizationButton = ({ memorized, onToggle }) => {
    return (React.createElement("button", { className: `py-2 px-4 rounded-full font-bold ${memorized ? 'bg-green-500' : 'bg-red-500'} text-white`, onClick: onToggle }, memorized ? 'Memorized' : 'Not Memorized'));
};
export default MemorizationButton;
